	import mixin from '@/common/mixin.js';
	import CanvasLine from '@/components/CanvasLine.vue';
	export default {
		data(){
			return{
				scrollPay: false,
				pay_Pop:false,
				mobile: ''
			}
		},
		components:{
			CanvasLine
		},
		mixins:[mixin],
		onPageScroll(obj){
			if(obj.scrollTop>800){
				this.scrollPay=true;
			}else{
				this.scrollPay=false;
			}
		},
		methods:{
			close(){
				this.pay_Pop=false;
			},
			openPay(){
				this.pay_Pop=true;
			},
			paySubmit(type){
				this.$http.post('/v5/pay/handle',{
					info_id: this.data.info_id,
					pay_method: type,
					plan_id: 'cm1',
					order_type: 'ceming'
				}).then(res=>{
					if(res.code==1){
						if(type=='alipay'){
							location.href = res.data.url;
                            return false;
						}else{
							localStorage.setItem('pay_result',JSON.stringify(res.data.result));
							location.href = res.data.url;
							return false;
						}
					}else{
						uni.showToast({
							title:res.msg,
							icon: 'none'
						})
					};
				})
			}
		}
	}
